.nav {


    -webkit-box-shadow: 0px 1px 5px 0px rgba(54, 54, 54, 1);
    -moz-box-shadow: 0px 1px 5px 0px rgba(54, 54, 54, 1);
    box-shadow: 0px 1px 5px 0px rgba(54, 54, 54, 1);
}

.logo-align {
    display: block;
    width: 15%;
    height: auto;
    margin-left: 4em;
}

@media (max-width: 768px) {
    .logo-align {
        margin: 0 auto;
        width: 45%;
        height: auto;
        padding-top: 20px;
        padding-bottom: 20px;
    }
}